/*   边框1px    start*/
.border {
  position: relative; }

.border a {
  position: relative; }

.border:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  width: 200%;
  height: 200%;
  -webkit-transform: scale(0.5);
  transform: scale(0.5);
  -webkit-transform-origin: left top;
  transform-origin: left top; }

/*   边框1px    end*/
/*   清除浮动    start*/
.clear:after {
  content: "";
  display: block;
  clear: both;
  visibility: hidden; }

/*   清除浮动    end*/
/*   列表点击背景    start*/
.act-list > li:active {
  background-color: rgba(0, 0, 0, 0.1); }

